import React from 'react';
import { Button, TabBar} from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont iconhome'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconhome'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={() => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                        
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont iconbiaoxing'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconbiaoxing'></i>                           
                        }
                        title="收藏"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                        if(this.props.info.username){
                            this.props.history.push('/cart')
                        }else{
                        this.props.history.push('/login')
                        }
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icondingdanliebiao'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icondingdanliebiao'></i>                           
                        }
                        title="订单列表"
                        key="cart"
                        selected={ pathname === '/dingdan' }
                        onPress={ () => {
                            this.props.history.push('/dingdan')
                        } }
                        data-seed="logId3"
                    ></TabBar.Item>
                     <TabBar.Item
                        icon={
                            <i className='iconfont iconwode'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconwode'></i>                           
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        data-seed="logId3"
                    ></TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
const mapStateToProps = (state) => {
    return {
        info:state.info
    }
}
export default withRouter(connect(mapStateToProps)(Mytab));