import { Grid } from 'antd-mobile'
import { data } from 'jquery'
import React from 'react'
import { connect, Provider } from 'react-redux'
import Mytab from './Mytab'
import store from './store'
import './CSS/cart.css'
class Cart extends React.Component {
    constructor(props) {
        super(props);
    }

    state = {
        data: [{ icon: '' }]

    }
    render() {
        setTimeout(() => {
            this.props.value.map((item) => {
                if (this.state.data[0].icon && this.state.data.length < this.props.value.length) {
                    this.setState({
                        data: [...this.state.data, {
                            icon: item,
                        }]
                    });
                } else if (!this.state.data[0].icon) {
                    this.setState({
                        data: [{
                            icon: item,
                        }]
                    })
                }
            })

        }, 10);
        return <Provider store={store}>

            <div>

                {this.state.data.map((item) => {
                    if (item.icon != "" && item.icon != undefined) {
                        return <div class="d-1">
                            <img class="i-1" src={item.icon} />
                            <button class="b-1">立即购买</button>
                            <button class="b-2" onClick={() => {
                                this.setState({
                                    data: [{ icon: '' }]
                                })
                                this.props.dispatch({
                                    type: 'DEL_TODO',
                                    icon: item
                                })
                                setTimeout(() => {
                                    this.props.value.map((item) => {
                                        if (this.state.data[0].icon && this.state.data.length < this.props.value.length) {
                                            this.setState({
                                                data: [...this.state.data, {
                                                    icon: item,
                                                }]
                                            });
                                        } else if (!this.state.data[0].icon) {
                                            this.setState({
                                                data: [{
                                                    icon: item,
                                                }]
                                            })
                                        }
                                    })

                                }, 10);
                            }}>取消收藏</button>
                        </div>
                    }
                })
                }

                <Mytab />
            </div>
        </Provider >
    }

}
const mapStateToProps = (state) => {
    return {
        value: state.todo
    }
}
export default connect(mapStateToProps)(Cart);
