import React from 'react'
import { connect } from 'react-redux'
import Mytab from './Mytab'
import './CSS/login.css'

const Login = (props) => {


    return (
        <div>
        {
            props.info.username?props.history.push('/'):<div>
            <p id="p-1">username:</p><input id="inp-1" type="text"></input>
            <p id="p-2">password:</p><input id="inp-2" type="password"></input>
            <button id="btn" onClick={()=>{
                props.dispatch({
                    type:'LOGIN',
                    username:'123'
                })
            }}>login</button></div>
        }
        </div>
    )
}
const mapStateToProps = (state) => {
    return {
        info:state.info
    }
}
export default connect(mapStateToProps)(Login)