import React from 'react';
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom';
import Home from './taobao/Home'
import Cart from './taobao/Cart'
import Ding from './taobao/Ding'
import My from './taobao/My'
import Search from './taobao/Search'
import Shangpin from './taobao/Shangpin'
import { Provider } from 'react-redux';
import store from './taobao/store';
import Login from './taobao/Login'
const App = () => {
	return <Provider store={store}>
	<Router>
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/cart' component={Cart} />
				<Route path='/dingdan' component={Ding} />
				<Route path='/my' component={My} />
				<Route path='/search' component={Search} />
				<Route path='/shangpin' component={Shangpin} />
				<Route path='/login' component={Login}/>
				<Route render={() => <Redirect to='/' />} />
			</Switch>
		</Router>
</Provider>

}
export default App;

