import React from 'react'
import { Carousel, Grid } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './CSS/home.css'
import Mytab from './Mytab'
import store from './store'
import { connect, Provider } from 'react-redux';
class Home extends React.Component {
    constructor(props) {
        super(props);
    }
    state = {
        data: ['https://gw.alicdn.com/imgextra/i4/117/O1CN01w8hBHE1CjcyBxK7WT_!!117-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i4/79/O1CN01xCsBma1CSDv73k0rz_!!79-0-lubanu.jpg',
            'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i3/6000000002143/O1CN01aPPNs71RhXOo7NnKr_!!6000000002143-0-octopus.jpg',
            'https://gw.alicdn.com/imgextra/i3/111/O1CN01fhP0rv1CgsatV3Imq_!!111-0-lubanu.jpg',
            'https://gw.alicdn.com/imgextra/i1/53/O1CN01GanI2E1CGJcfKOxML_!!53-0-lubanu.jpg',
            'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
            'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i3/6000000002143/O1CN01aPPNs71RhXOo7NnKr_!!6000000002143-0-octopus.jpg',
            'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg'],
        imgHeight: 176,
        data1: [{
            icon: '',
            text: ''
        }]
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({
                data: ['https://gw.alicdn.com/imgextra/i4/117/O1CN01w8hBHE1CjcyBxK7WT_!!117-0-lubanu.jpg',
                    'https://gw.alicdn.com/imgextra/i4/79/O1CN01xCsBma1CSDv73k0rz_!!79-0-lubanu.jpg',
                    'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i3/6000000002143/O1CN01aPPNs71RhXOo7NnKr_!!6000000002143-0-octopus.jpg',
                    'https://gw.alicdn.com/imgextra/i3/111/O1CN01fhP0rv1CgsatV3Imq_!!111-0-lubanu.jpg',
                    'https://gw.alicdn.com/imgextra/i1/53/O1CN01GanI2E1CGJcfKOxML_!!53-0-lubanu.jpg',
                    'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
                    'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i3/6000000002143/O1CN01aPPNs71RhXOo7NnKr_!!6000000002143-0-octopus.jpg',
                    'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg'],
            });
        }, 100);
        setTimeout(() => {
            let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
            fetch(url)
                .then(res => res.json())
                .then((res) => {

                    this.props.dispatch({
                        type: 'TODOLIST',
                        value: res
                    })


                })


        }, 100)
    }
    render() {

        setInterval(() => {
            this.props.value.map((item) => {
                if (this.state.data1[0].icon && this.state.data1.length < 10) {
                    this.setState({
                        data1: [...this.state.data1, {
                            icon: item.img,
                            text: item.title + '￥' + item.price
                        }]
                    })
                } else if (!this.state.data1[0].icon) {
                    this.setState({
                        data1: [{
                            icon: item.img,
                            text: item.title + '￥' + item.price
                        }]
                    })
                }
            })
        }, 10)
        return <Provider store={store}>
            <div>
                <header className="sc-VigVT SQtZc iconfont icontaobao2">
                    <div className="header-bd">
                        <a className="placeholder" onClick={() =>
                            this.props.history.push('/search')}>
                            <span className="text iconfont iconsearch">
                                寻找宝贝店铺
                            </span>
                        </a>
                    </div>
                </header>
                <Carousel
                    autoplay={true}
                    infinite={true}
                    slideWidth={1}
                >
                    {this.state.data.map(val => (
                        <a
                            key={val}
                            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                            <img
                                src={`${val}`}
                                alt=""
                                style={{ width: '100%', marginTop: '38px' }}
                                onLoad={() => {
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                    ))}
                </Carousel>
                <div className="recommend-hd">
                    <img className="recommend-hd-bg"
                        src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png"></img>
                </div>
                <div className="recommend-container">
                    <Grid data={this.state.data1}
                        columnNum={2}
                        itemStyle={{ height: '300px', width: '150px', background: 'rgba(0,0,0,.05)' }}
                        square={false}
                        onClick={(data) => {
                            console.log(this.props.info.username)
                            if (this.props.info.username) {
                                this.props.history.push({
                                    pathname: '/shangpin',
                                    state: data.icon
                                })
                            } else {
                                this.props.history.push({
                                    pathname: '/login',
                                })
                            }
                        }
                        }
                    />
                </div>
                <Mytab />

            </div>
        </Provider>
    }
}
const mapStateToProps = (state) => {
    return {
        value: state.home,
        info: state.info
    }
}
export default withRouter(connect(mapStateToProps)(Home))
