let todo = new Array();
let l = 0, j = 0, k = 0, m = 0;
function todoreducer(state = todo, action) {
    if (action.type == 'ADD_TODO') {
        for (m = 0; m < l; m++) {
            if (todo[m] == action.img.location.state) {
                return todo
            }
        }
        todo[l] = action.img.location.state;
        l++;
        return todo
    }
    if (action.type == "DEL__TODO") {
        for (j = 0; j < l; j++) {
            if (todo[j] == action.img.location.state) {
                for (k = j; k < l; k++) {
                    todo[k] = todo[k + 1];
                }
                l--;
            }
        }
        return todo
    }
    if (action.type == 'DEL_TODO') {

        for (j = 0; j < l; j++) {
            if (todo[j] == action.icon.icon) {
                for (k = j; k < l; k++) {
                    todo[k] = todo[k + 1];
                }
                l--;
            }
        }
        return todo
    }
    return state;
}

export default todoreducer;