import { ActionSheet } from 'antd-mobile'
import React from 'react'
let todolist = []
const Homereducer = (state = todolist, action) => {
    switch (action.type) {
        case 'TODOLIST':
            let i = 0;
            action.value.data.map((item) => {
                todolist[i] = {
                    id: item.id,
                    img: item.img,
                    title: item.title,
                    price: item.price
                }
                i++;
            })
            if (todolist.length == 10) {
                return todolist;
            }
        default:
            return state;
    }
}

export default Homereducer;

