import { Icon, NavBar, TabBar } from 'antd-mobile';
import React from 'react'
import './CSS/shangpin.css'

import { connect, Provider } from 'react-redux';
import store from './store'

const Shangpin = (props) => {

    let a = 'iconfont iconbiaoxing img1',
        i = 1

    return <Provider store={store}>
        <div style={{
            position: 'fixed',
            width: '100%',
            height: '44.7%'
        }}>
            <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => props.history.push('/')}
            />
            <img src={props.location.state} style={{
                width: '100%',
                height: '400px',
            }} />
            <div className="div1">
                <div className="div2">
                    <span className="iconfont icondianpu img1" />
                    <p className="p1">店铺</p>
                </div>
                <div className="div2">
                    <span className="iconfont iconkefu img1" />
                    <p className="p1">客服</p>
                </div>
                <div className="div2" id="par">
                    <span className={a} id="shouc" onClick={() => {
                        if (i == 1) {
                            a = a.replace('iconbiaoxing', 'iconbiaoxingfill')
                            props.dispatch({
                                type: 'ADD_TODO',
                                img: props

                            })

                            i--;
                        } else if (i == 0) {
                            a = a.replace('iconbiaoxingfill', 'iconbiaoxing')
                            props.dispatch({
                                type: 'DEL__TODO',
                                img: props
                            })
                            i++;
                        }

                        let sho = document.getElementById('shouc');
                        sho.className = a;
                    }} />
                    <p className="p1">收藏</p>
                </div>
                <div className="div3">
                    <div className="img2"><p className="p2">加入购物车</p></div>
                </div>
                <div className="div3">
                    <div className="img3"><p className="p2">立即购买</p></div>
                </div>
            </div>
        </div>
    </Provider>
}
const mapStateToProps = (state) => {
    return {
        img: state.todo,
        
    }
}
export default connect(mapStateToProps)(Shangpin);


